.progressBar {
    width: 100%;
    height: 20px;
    border: none;
    background-color: red;
    border: 0px solid red;
}

progress[value]::-webkit-progress-bar {
    background-color: #1d293a;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
}
progress::-webkit-progress-value {
    float: left;
    height: 15px;
    margin: 0;
    padding-left: 10px;
    /* Rounded corners requires a padding */
    background: #20bb45;
    border-radius: 10px;
}

.progressDiv {
    border: 0px solid red;
    position: absolute;
    width: 100%;
    bottom: 0;
}
@media screen and (max-width:500px) {
    .progressBar {
        height: 10px;
    }
}